.contacts{
  &__items{
    display: grid;
    grid-template-columns: 14.375em calc(100% - 37.5em) 21.875em;
    gap: .625em;
    align-items: flex-start;
    margin-top: 1.875em;
  }
}

.contact-item{
  padding-left: .625em;
  @include grid100gap(1.25em);
  &__title{
    opacity: .5;
  }
  &__desc{
    @include grid100gap(.625em);
  }
}

.requisites{
  margin-top: 3.75em;
  .text{
    margin-top: 1.875em;
    max-width: 56.25em;
  }
  &__items{
    margin-top: 2.5em;
    @include grid100gap(1.875em);
  }
}

.requisite-item{
  display: grid;
  grid-template-columns: 16em calc(100% - 16em);
  align-items: center;
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 120%;
  }
  &__title{
    color: $primary;
  }
  &__desc{
    &-item{
      display: block;
      line-height: inherit;
      margin-bottom: 1em;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__items{
      grid-template-columns: 100%;
      gap: 1.875em;
      margin-top: 1.25em;
    }
  }

  .contact-item{
    padding-left: 0;
  }

  .requisites{
    margin-top: 2.5em;
    .text,
    &__items{
      margin-top: 1.25em;
    }
  }

  .requisite-item{
    grid-template-columns: 100%;
    gap: .625em;
  }
}


@media screen and (max-width: 576px){
  .contact-item{
    padding-left: 0;
    &__desc{
      span{
        display: block;
        line-height: inherit;
      }
    }
  }
}
