.about-page{
  &__grid{
    display: grid;
    grid-template-columns: 21.875em calc(100% - 22.5em);
    gap: 1.25em .625em;
  }
}

.about-nav{
  &__links{
    padding: .5em;
    border-radius: .9375em;
    background-color: $grayBg;
  }
  &__current,
  &__link{
    border-radius: .625em;
    height: 3.75em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2em;
    font-size: 1em;
    line-height: 120%;
    font-weight: 500;
    &:after{
      content: '';
      width: 1.5em;
      height: 1.5em;
      flex-shrink: 0;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 18L15 12L9 6' stroke='%23465262' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
      @include bgImgSize(100%);
      margin-left: 1em;
      transition: inherit;
    }
  }
  &__current,
  &__link:hover,
  &__link._active{
    background-color: $primary;
    color: $white;
    &:after{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 18L15 12L9 6' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
    }
  }
  &__current{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .about-page{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .about-nav{
    padding: .5em;
    border-radius: .9375em;
    background-color: $grayBg;
    position: relative;
    &._open{
      .about-nav__current{
        &:after{
          transform: rotate(-90deg);
        }
      }
      .about-nav__links{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__current{
      display: flex;
      position: relative;
      z-index: 4;
      cursor: pointer;
      &:after{
        transition: all .3s;
        transform: rotate(90deg);
      }
    }
    &__links{
      z-index: 3;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      padding-top: 4.25em;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &__link{
      &._active{
        display: none;
      }
    }
  }
}


