.story-modal{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: calc(100 * var(--vh));
  padding: 8.75em 3.625em;
  opacity: 0;
  pointer-events: none;
  z-index: 20;
  &._active {
    opacity: 1;
    pointer-events: unset;
  }
  &__dialog{
    height: 100%;
    width: 74.375em;
    max-width: 100%;
    margin: 0 auto;
    padding-bottom: 4.375em;
  }
}

.story-slider{
  height: 100%;
  position: relative;
  max-height: 45.25em;
  .swiper{
    &-container{
      height: 100%;
    }
    &-slide{
      &-active{
        .story-item{
          &:after{
            display: none;
          }
        }
        .story-slider__progress{
          display: block;
        }
      }
      &-prev{
        .story-item{
          &:after{
            background: linear-gradient(90deg, rgba(#050a10,0) 0%, #050a10 100%);
          }
        }
      }
    }
    &-button{
      top: 50%;
      margin: 0;
      transform: translateY(-50%);
      &-prev{
        left: -3.125em;
      }
      &-next{
        right: -3.125em;
      }
    }
  }
  &__progress{
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: calc(100% - .625em);
    height: .625em;
    border-radius: 2.5em;
    background-color: rgba($white,.2);
    margin-top: 3.75em;
    span{
      transition: all .1s;
      position: absolute;
      top: 0;
      left: 0;
      border-radius: inherit;
      height: 100%;
      background-color: $white;
    }
  }
}

.swiper-button{
  width: 3.125em;
  @include center;
  svg{
    width: 100%;
  }
}

.story-item{
  height: 100%;
  &:after{
    opacity: .8;
  }
  &__img{
    height: calc(100% - 3.25em);
    @include imgCover(100%);
    border-radius: .9375em;
    padding-bottom: 0;
    max-height: 42em;
  }
  &__badge{
    height: 2.625em;
    @include center;
    margin-top: .625em;
    background-color: $white;
    border-radius: .9375em;
    font-weight: 700;
    span{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .story-modal{
    padding: 6.25em .625em;
    &__dialog{
      padding: 0;
    }
  }

  .story-slider{
    .swiper{
      &-button{
        display: none;
      }
    }
    &__progress{
      top: unset;
      bottom: 100%;
      margin: 0 0 1.875em;
    }
  }

  .story-item{
    &__img{
      height: calc(100% - 2.9375em);
      border-radius: .625em;
    }
    &__badge{
      height: 2.3125em;
      border-radius: .625em;
      span{
        font-size: .875em;
      }
    }
  }
}


