.tab{
  transition: all .3s;
  font-weight: 400;
  text-transform: unset;
  height: 4.375em;
  span{
    font-size: 1.25em;
  }
  &:hover{
    cursor: pointer;
  }
  &._active{
    font-weight: 700;
    background: $primary;
    color: $white;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
