.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .95;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 44.375em;
        max-width: calc(100% - 1.25em);
        position: relative;
        z-index: 3;
        padding: 2.5em;
        border-radius: .9375em;
        display: inline-block;
    }
    &__info{
        @include grid100gap(1.25em);
    }
    &__title{

    }
    @media screen and (min-width: 993px){
        .h2{
            font-size: 2.5em;
        }
    }
    &__desc{
        line-height: 140%;
    }
    .order-form{
        margin-top: 2.5em;
    }
}

.modal-thank{
    .modal__dialog{
        background-color: $primary;
        padding: 0;
    }
    .success-form{
        position: static;
        padding: 2.5em;
        border-radius: inherit;
        height: unset;
        min-height: unset;
        width: 100%;
        opacity: 1;
        pointer-events: unset;
        &__content{
            max-width: 30.25em;
        }
    }
}

.close-btn{
    width: 1.4375em;
    @include center;
    position: absolute;
    right: 1.875em;
    top: 1.875em;
    svg{
        width: 100%;
    }
    &:hover{
        path{
            stroke: $blue;
        }
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            padding: 2.5em 1.25em;
        }
        .order-form{
            margin-top: 1.875em;
        }
    }

    .modal-thank{
        .success-form{
            padding: 2.5em 1.25em;
        }
    }

    .close-btn{
        top: 1.25em;
        right: 1.25em;
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }
}
