.text{
  h3{
    font-size: 1.875em;
    line-height: 120%;
    font-weight: 700;
    margin-bottom: 1em;
  }
  li,
  p{
    font-size: 1.125em;
    line-height: 140%;
  }
  p{
    @include lastM(0 0 1.5em);
  }
  img{
    width: 100%;
    margin: 1.875em 0;
    border-radius: .9375em;
    display: block;
    &:first-child{
      margin-top: 0;
    }
    &:last-child{
      margin-bottom: 0;
    }
  }
  ul{
    @include grid100gap(.625em);
    li{
      padding-left: 1.375em;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        width: .75em;
        height: .75em;
        border-radius: 50%;
        background-color: $primary;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  &._small{
    li,
    p{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .text{
    h3{
      font-size: 1.25em;
    }
    p{
      font-size: 1em;
    }
    img{
      margin-top: 1.25em;
    }
  }
}


