.offer{
  position: relative;
  overflow: unset;
  &__grid{
    padding-top: 4.375em;
    padding-bottom: 4.375em;
    display: grid;
    justify-content: space-between;
    grid-template-columns: 40em 36.875em;
    gap: 1.25em;
  }
}

.offer-info{
  @include grid100gap(4em);
  &__desc{
    max-width: 36.875em;
    line-height: 140%;
  }
}

.order-form{
  @include grid100gap(.625em);
  &__btn,
  .m-check{
    margin-top: .625em;
  }
}

.success-form{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  right: .625em;
  min-height: calc(100% - 1.25em);
  width: 50%;
  background-color: $primary;
  background-image: url(../img/check-circle.svg);
  background-repeat: no-repeat;
  background-position: right top;
  background-size: auto 100%;
  top: .625em;
  z-index: 3;
  border-radius: .9375em;
  padding: 2.5em 1em 2.5em 7.5em;
  color: $white;
  &__content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 36.25em;
  }
  &__desc,
  &__desc3{
    margin-top: .625em;
  }
  &__desc2{
    margin-top: 1.75em;
  }
  .req__link{
    margin-top: .625em;
    path{
      stroke: $white;
    }
    &:hover{
      color: $white;
      opacity: .8;
    }
  }
}

.form-block{
  &._is-success{
    .success-form{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.banks{
  padding-bottom: 5em;
  .swiper-slide{
    width: 19.875em;
  }
  .brand-card{
    height: 5.875em;
  }
}

.offer2{
  @media screen and (min-width: 993px){
    .success-form{
      min-height: 24.0625em;
    }
  }
}


@media screen and (max-width: 992px){
  .offer{
    overflow: hidden;
    &__grid{
      padding: 2.5em 0;
      grid-template-columns: 100%;
    }
  }

  .offer-info{
    gap: 1.25em;
  }

  .form-block{
    position: relative;
  }

  .success-form{
    min-height: calc(100% + 1.25em);
    width: 100%;
    top: -.625em;
    right: 0;
    padding: 1.25em;
    border-radius: .625em;
    .h2,
    .h3 {
      font-size: 1.25em;
      max-width: 11em;
    }
    &__desc2{
      margin-top: 2.5em;
    }
    .req__link{
      margin-top: 1.25em;
      svg{
        width: 1.875em;
      }
    }
  }

  .banks{
    padding-bottom: 2.5em;
    margin-top: -1.25em;
    .swiper-slide{
      width: 11.5em;
    }
    .brand-card{
      height: 3.75em;
      padding: 1em;
    }
  }
}


