.header{
  padding: 1.5em 0 .875em;
  background-color: $gray;
  color: $white;
  border-radius: .9375em;
  position: relative;
  z-index: 5;
  &__inner{
    position: relative;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: .875em;
    margin-top: .875em;
    border-top: 1px solid rgba($white,.2);
  }
  &__top{
    min-height: 1.375em;
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 14.6875em;
    img{
      width: 100%;
      display: block;
    }
  }
  .menu{
    display: flex;
    align-items: center;
    font-weight: 700;
    li{
      margin-right: 2.5em;
      &:last-child{
        margin-right: 0;
      }
    }
    @media screen and (min-width: 993px){
      .current-menu-item{
        a{
          text-decoration: underline;
          color: $primary;
        }
      }
    }
  }
  .phone-link{
    font-weight: 700;
  }
  &._open{
    .catalog-menu{
      opacity: 1;
      pointer-events: unset;
    }
    .catalog-btn{
      background-color: $white;
      color: $gray;
      &:hover{
        background-color: $white;
        color: $gray;
      }
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
}

.header-search{
  margin-left: 1.5em;
  position: relative;
  &._open{
    .header-search__field{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__field{
    position: absolute;
    top: 0;
    right: 100%;
    margin-right: .625em;
    width: 50.25em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    input{
      padding: 0 1.875em;
      height: 3.75em;
    }
  }
  &__btn{
    padding: 0;
    width: 3.75em;
    height: 3.75em;
    img{
      width: 1.5em;
    }
  }
}

.catalog-btn{
  cursor: pointer;
  &__icon{
    width: 1.5em;
    height: 1.5em;
    flex-shrink: 0;
    position: relative;
    margin-right: .625em;
    img{
      @include centerAbsolute;
      width: 100%;
    }
    ._view2{
      opacity: 0;
    }
  }
}

.catalog-menu{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  top: 100%;
  margin-top: .625em;
  left: 0;
  width: 100%;
  background-color: $gray;
  padding: 1.25em 0;
  color: $gray;
  border-radius: .9375em;
  .category-card{
    display: flex;
    flex-direction: column;
    height: 9.25em;
    position: relative;
    &__img{
      height: calc(100% - 1.875em);
      top: .9375em;
      right: 3.75em;
      width: 14.75em;
      position: absolute;
    }
    &__price{
      display: none;
    }
    &__nav{
      display: flex;
      padding-top: 1em;
      margin-top: auto;
    }
    &__title{
      max-width: 12em;
      .text30{
        font-weight: 600;
        font-size: 1.25em;
      }
    }
  }
}

.header-contacts{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  @media screen and (min-width: 993px){
    right: 0;
    top: 0;
    position: absolute;
    &__time{
      br{
        display: none;
      }
    }
    &__location{
      margin-right: 2.625em;
      display: flex;
      align-items: center;
    }
    &__streets{
      display: flex;
      margin-left: 1.25em;
    }
    &__street{
      &:not(:last-child){
        margin-right: .625em;
        padding-right: .625em;
        border-right: 1px solid $white;
      }
    }
  }
  &__street{
    font-weight: 600;
  }
}

.mob-menu{
  display: none;
}


@media screen and (max-width: 992px){
  .header {
    padding: .625em 0 .75em;
    border-radius: .625em;
    position: fixed;
    z-index: 5;
    top: .625em;
    left: .625em;
    width: calc(100% - 1.25em);
    box-shadow: 0 8px 18px 0 rgba(0, 0, 0, 0.1), 0 33px 33px 0 rgba(0, 0, 0, 0.09), 0 73px 44px 0 rgba(0, 0, 0, 0.05), 0 130px 52px 0 rgba(0, 0, 0, 0.01), 0 204px 57px 0 rgba(0, 0, 0, 0);

    &__inner{
      position: static;
    }
    &__top{
      min-height: unset;
    }
    &__bottom {
      margin-top: .625em;
      padding-top: .75em;
    }
    &__logo{
      width: 8.5em;
    }
    .phone-link{
      svg{
        display: none;
      }
      span{
        font-size: .75em;
      }
    }
    &._open{
      .m-menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        color: $primary;
        path{
          stroke: $primary;
        }
      }
      &~.footer .mob-menu__bg{
        display: block;
      }
    }
  }

  .m-menu{
    position: absolute;
    top: -.625em;
    left: 0;
    width: 100%;
    padding-top: 7em;
    padding-bottom: 4.875em;
    height: $vh;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__inner{
      height: 100%;
      overflow-y: auto;
      border-radius: .625em;
      background-color: $gray;
      padding: .5em;
      display: flex;
      flex-direction: column;
    }
    .menu{
      display: block;
      li{
        margin: 0;
        border-bottom: 1px solid rgba($white,.1);
        &:last-child{
          border-bottom: none;
        }
      }
      a{
        padding: 0 .625em;
        height: 3.75em;
        display: flex;
        align-items: center;
        justify-content: space-between;
        &:after{
          content: '';
          width: 1.5em;
          height: 1.5em;
          flex-shrink: 0;
          background-image: url(../img/arrow-right3.svg);
          @include bgImgSize(100%);
          margin-left: 1em;
        }
      }
    }
  }

  .header-search{
    margin-left: 1.25em;
    &__field{
      width: calc(100vw - 5.625em);
      input{
        font-size: .875em;
        padding: 0 .71em;
        border-radius: .71em;
        height: 2.86em;
      }
    }
    &__btn{
      width: 2.5em;
      height: 2.5em;
      img{
        width: 1.25em;
      }
    }
  }

  .catalog-btn,
  .catalog-menu{
    display: none;
  }

  .header-contacts{
    padding: 2em .625em .75em;
    margin-top: auto;
    width: 100%;
    display: block;
    &__time{
      margin-top: 1.25em;
      padding-top: 1.25em;
      border-top: 1px solid rgba($white,.1);
    }
    &__streets{
      @include grid100gap(.625em);
      margin-top: 1.25em;
    }
  }

  .mob-menu{
    display: block;
    position: fixed;
    z-index: 10;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: .625em;
    background-color: $white;
    &__bg{
      height: $vh;
      background-color: $white;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 4;
      position: fixed;
      display: none;
    }
    &__inner{
      background-color: $gray;
      border-radius: .625em;
      padding: .625em 0;
      display: grid;
      grid-template-columns: repeat(4,25%);
    }
    &__btn{
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      color: $white;
      svg{
        height: 1.25em;
        margin-bottom: .125em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{

  }
}
