.service-desc{
  .text{
    p{
      font-size: 1.375em;
    }
  }
}

.equipment{
  margin-top: 5em;
}

.equipment-slider2{
  margin-top: 1.875em;
  .swiper-slide{
    width: calc(100% / 3);
  }
}

.brand-top{
  padding-bottom: 0;
  @media screen and (min-width: 993px){
    background-image: url(../img/brand-bg.svg);
    background-size: 81.25em auto;
    background-position: right bottom;
  }
}

.brand-info{
  &__img{
    width: 10.25em;
    height: 5.3125em;
    border-radius: .9375em;
    background-color: $white;
    @include center;
    margin-left: 1em;
    padding: .875em;
    flex-shrink: 0;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.brand-car{
  margin-bottom: -5em;
  display: flex;
  justify-content: center;
  img{
    width: 125em;
    display: block;
  }
}


@media screen and (max-width: 1700px){
  .brand-car{
    img{
      width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .service-desc{
    .text{
      p{
        font-size: 1em;
      }
    }
  }

  .equipment-slider2{
    margin-top: 1.25em;
    .swiper-slide{
      width: 18.75em;
    }
  }

  .brand-info{
    flex-direction: column;
    align-items: flex-start;
    &__img{
      margin: 1em 0 0;
    }
  }

  .brand-top{
    padding-bottom: 1em;
  }

  .brand-car{
    margin-bottom: 0;
    margin-top: 2em;
  }
}


