.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  .nice-select,
  input,
  textarea{
    border-radius: .625em;
    border: 1px solid #EBEBEB;
    padding: 0 2em;
    height: 3.5em;
    width: 100%;
    font-size: 1em;
    background-color: $white;
    color: $black;
    &::placeholder {
      color: #CDCDCD;
    }
    &:focus {
      border-color: $black;
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 2.5em;
    min-height: 1.5em;
    span{
      font-size: .75em;
      line-height: 120%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid #EBEBEB;
      width: 1.5em;
      height: 1.5em;
      @include bgImgSize(0);
      transition: all .0s;
      background-color: $white;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        //background-color: $primary;
        border-color: $primary;
        background-size: 1.25em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .3125em;
      background-image: url(../img/check.svg);
    }
  }
}



