.credit{
  margin-top: 5.625em;
  &__top{
    display: grid;
    grid-template-columns: 36.25em calc(100% - 37.5em);
    gap: 1.25em;
    align-items: center;
  }
  &__items{
    margin-top: 1.875em;
  }
}

.product-detail{
  padding: 2.5em 0;
  margin-top: 1.25em;
  .tabs{
    margin-bottom: .625em;
  }
}

.mob-tab{
  &__show{
    display: none;
  }
  &__hidden{
    padding: 1.25em;
    border-radius: .9375em;
    background-color: $white;
    @media screen and (min-width: 993px){
      display: block !important;
    }
  }
}

.props{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 1.875em));
  gap: 2.5em;
}

.props-group{
  &__items{
    margin-top: 1.25em;
    @include grid100gap(.625em);
  }
}

.prop-item{
  display: flex;
  &:after{
    content: '';
    flex-grow: 1;
    min-width: 2em;
    align-self: flex-end;
    border-bottom: 1px dashed rgba($gray,.2);
    margin: 0 .625em .4375em;
  }
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 140%;
  }
  &__desc{
    order: 2;
  }
}

.product-equip{
  max-width: 70em;
  column-count: 3;
  column-gap: 5em;
  &__item{
    margin-bottom: .625em;
    display: inline-block;
    width: 100%;
    font-size: 1em;
    line-height: 140%;
  }
}


@media screen and (max-width: 992px){
  .credit{
    margin-top: 2.5em;
    &__top{
      grid-template-columns: 100%;
    }
    &__items{
      margin-top: 1.25em;
    }
  }

  .product-detail{
    padding: .5em;
    margin-top: 0;
    .container{
      padding: 0;
    }
    .tabs{
      display: none;
    }
    .tab-content{
      display: block;
    }
    .text{
      p{
        font-size: .875em;
      }
    }
  }

  .mob-tab{
    &._open{
      .mob-tab__show{
        background-color: $primary;
        color: $white;
        svg{
          transform: rotate(-180deg);
        }
        path{
          stroke: $white;
        }
      }
    }
    &__show{
      transition: all .3s;
      display: flex;
      justify-content: space-between;
      text-transform: unset;
      font-weight: 500;
      height: 3.75em;
      cursor: pointer;
      svg{
        width: 1.5em;
      }
    }
    &__hidden{
      display: none;
      margin-top: .625em;
    }
  }

  .props{
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
  }

  .prop-item{
    display: flex;
    &:after{
      margin-bottom: .3125em;
    }
    &__title,
    &__desc{
      font-size: .875em;
    }
  }

  .product-equip{
    column-count: 2;
    column-gap: 2em;
  }
}


@media screen and (max-width: 576px){
  .credit{
    .brand-card{
      padding: .625em;
      height: 3.75em;
    }
  }

  .props{
    grid-template-columns: 100%;
  }

  .product-equip{
    column-count: 1;
  }
}
