.services{
  &__grid{
    margin-top: 1.875em;
  }
}

.service-card{
  padding: 2.5em 1.875em;
  border-radius: .9375em;
  background-color: $grayBg;
  display: flex;
  flex-direction: column;
  min-height: 23.375em;
  &__desc{
    margin-top: 1.875em;
  }
  &__nav{
    padding-top: 1.25em;
    margin-top: auto;
  }
}

.brands-slider2{
  .swiper-slide{
    width: 25%;
  }
  .brand-card{
    @media screen and (min-width: 993px){
      height: 6.625em;
      border-radius: .9375em;
    }
  }
}

.service-info{
  margin-top: 1.25em;
  display: grid;
  grid-template-columns: 53em calc(100% - 54.25em);
  gap: 1.25em;
  align-items: center;
  &__col2{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__btn{
    margin-left: 1.25em;
  }
}

.phone-link{
  display: flex;
  align-items: center;
  font-weight: 800;
  &:hover{
    color: $primary;
    path{
      stroke: $primary;
    }
  }
  svg{
    width: 1.875em;
    margin-right: .625em;
  }
  span{
    font-size: 1.25em;
    line-height: 140%;
  }
}


@media screen and (max-width: 992px){
  .services{
    &__grid{
      margin-top: 1.25em;
    }
  }

  .service-card{
    padding: 1.25em .625em;
    border-radius: .625em;
    min-height: 18em;
    &__desc{
      margin-top: 1.25em;
    }
  }


  .brands-slider2{
    .swiper-slide{
      width: 15em;
    }
  }

  .service-info{
    grid-template-columns: 100%;
    &__col2{
      justify-content: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .service-info{
    &__col2{
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn{
      margin: 1em 0 0;
    }
  }
}
