.m-section{
  margin-top: .625em;
  border-radius: .9375em;
  overflow: hidden;
  &--padding{
    padding: 5em 0;
  }
  &._gray-dark{
    background-color: $gray;
    color: $white;
  }
  &._gray{
    background-color: $grayBg;
  }
}

.office{
  &__grid{
    display: grid;
    grid-template-columns: 36.875em calc(100% - 37.5em);
    gap: .625em;
  }
  &__content{
    position: relative;
    min-height: 46.875em;
    padding: 5em .625em 5em 0;
    &:before{
      content: '';
      position: absolute;
      height: 100%;
      width: 200em;
      z-index: -1;
      background-color: $grayBg;
      right: 0;
      top: 0;
      border-radius: .9375em;
    }
  }
  &__map{
    width: calc(100vw - (100vw - 1450px) / 2 - 1.875em - 37.5em);
    height: 100%;
    iframe{
      width: 100%;
      height: 100%;
      border-radius: .625em;
      border: none !important;
    }
  }
  &__items{
    @include grid100gap(1.875em);
  }
}

.office-item{
  @include grid100gap(.625em);
}

.about{
  padding: .625em 0;
  &__grid {
    display: grid;
    grid-template-columns: 44.375em calc(100% - 45em);
    gap: .625em;
  }
  &__content {
    min-height: 37.5em;
    padding: 5em 0;
    display: flex;
    flex-direction: column;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.25em;
  }
  &__img{
    width: calc(100vw - (100vw - 1450px) / 2 - 3.125em - 45em);
    height: 100%;
    img{
      max-height: 37.5em;
      width: 100%;
      min-height: 100%;
      object-fit: cover;
      display: block;
      border-radius: .9375em;
    }
  }
  .text{
    a{
      text-decoration: underline;
      color: #5599EB;
      &:hover{
        color: $primary;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    border-radius: .625em;
    &--padding{
      padding: 2.5em 0;
    }
  }

  .office{
    padding: 2.5em 0 .625em;
    background-color: $grayBg;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__content{
      min-height: unset;
      padding: 0;
    }
    &__map{
      height: 25em;
      width: 100%;
      iframe{
        border-radius: .625em;
      }
    }
    &__items{
      margin-top: 1.25em;
      gap: 1.25em;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__link{
      width: 100%;
    }
  }

  .about{
    padding-top: 2.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__content{
      padding: 0;
      min-height: unset;
    }
    &__img{
      width: 100%;
      img{
        border-radius: .625em;
      }
    }
    &__link{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .office{
    &__map{
      height: 15em;
    }
  }
}
