.product-top{
  @media screen and (min-width: 993px){
    background-image: url(../img/product-bg.svg);
    background-size: 63em auto;
    background-position: right bottom;
  }
  &__wrap{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__col{
    max-width: 62.5em;
  }
  &__info{
    margin-top: 1.75em;
  }
  .m-badge{
    height: 2.5em;
    font-weight: 600;
    margin-right: 1.875em;
  }
  &__price{
    font-size: 2.5em;
    line-height: 120%;
    font-weight: 700;
  }
  &__btn{
    margin-right: 1.25em;
    &:last-child{
      margin: 0;
    }
  }
}

.gallery{
  .swiper-slide{
    width: calc(100% / 3);
  }
}

.gallery-slider{
  position: relative;
  .swiper{
    &-button{
      top: 50%;
      margin: 0;
      transform: translateY(-50%);
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
  }
}

.gallery-card{
  @include imgCover(63.5%);
  border-radius: .9375em;
  &:after{
    content: '';
    position: absolute;
    top: .625em;
    right: .625em;
    background-image: url(../img/search-icon.svg);
    @include bgImgSize(100%);
    width: 1.5em;
    height: 1.5em;
    transition: all .3s;
    opacity: .6;
  }
  &:hover{
    &:after{
      opacity: 1;
    }
  }
}

.advantage-item{
  padding: 1.25em;
  border-radius: .625em;
  background-color: $grayBg;
}


@media screen and (max-width: 992px){
  .product-top{
    background-image: unset;
    &__wrap{
      display: block;
    }
    &__info{
      margin-top: .625em;
    }
    .m-badge{
      height: 2.0625em;
      font-weight: 400;
      margin-right: .625em;
      img{
        width: 1em;
      }
      span{
        font-size: .875em;
      }
    }
    &__price{
      font-size: 1.25em;
    }
    &__col2{
      margin-top: 1.25em;
      @include grid100gap(.625em);
    }
    &__btn{
      width: 100%;
      margin: 0;
    }
  }

  .gallery{
    @include fullScreen;
    border-radius: 0;
    .swiper-slide{
      width: 20.375em;
    }
    .swiper-button{
      display: none;
    }
  }

  .gallery-card{
    border-radius: .625em;
  }

  .advantage-item{
    padding: .625em;
  }
}


@media screen and (max-width: 576px){
  .advantages{
    gap: .25em;
  }
}
