.category-card{
  padding: 4.125em .625em .625em;
  border-radius: .9375em;
  background-color: $grayBg;
  display: flex;
  flex-direction: column;
  position: relative;
  &__top{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: .625em;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__title{
    padding-top: .4375em;
  }
  &__count{
    height: 3.125em;
    min-width: 3.125em;
    @include center;
    padding: 0 .625em;
    border-radius: .625em;
    background-color: $gray;
    color: $white;
  }
  &__img{
    height: 13.75em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__price{
    margin-top: .5em;
    padding: 0 .625em;
  }
  &__nav{
    padding-top: .5em;
    margin-top: auto;
  }
}

.product-desc{
  @media screen and (min-width: 993px){
    .about{
      &__grid {
        grid-template-columns: 52em calc(100% - 52.625em);
      }
      &__content{
        max-width: 44.375em;
      }
      &__img {
        width: calc(100vw - (100vw - 1450px) / 2 - 3.125em - 52.625em);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .category-card{
    border-radius: .625em;
    padding: 3.375em 1.25em 1.25em;
    &__top{
      padding: .625em .625em .625em 1.25em;
    }
    &__title{
      padding-top: 0;
    }
    &__count{
      padding: 0 .3125em;
      height: 2.5em;
      min-width: 2.5em;
    }
    &__img{
      height: 10em;
    }
    &__price{
      padding: 0;
    }
  }
}


