.equipment-brand{
  padding-bottom: 4.375em;
}

.equipment-call{
  padding: 2.5em 0;
  &__content{
    @include grid100gap(.625em);
  }
  &__title{
    font-size: 2em;
  }
  .req__link{
    margin: 0 0 0 2em;
  }
}

.equipment2-card{
  padding: 1.25em 2.5em 2.5em;
  border-radius: 1.125em;
  background-color: $grayBg;
  &__img{
    height: 9.375em;
    display: flex;
    align-items: center;
    margin-bottom: .9375em;
    img{
      max-width: 100%;
      max-height: 100%;
      mix-blend-mode: multiply;
    }
  }
  &__links{
    margin-top: 1.25em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-size: 1.25em;
    line-height: 140%;
    margin-top: .75em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__more{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .equipment-brand{
    padding-bottom: 0;
  }

  .equipment-call{
    padding: 1.25em 0;
    &__inner{
      flex-direction: column;
      align-items: flex-start;
    }
    &__title{
      font-size: 1.25em;
    }
    .req__link{
      margin: 1.25em 0 0;
    }
  }

  .equipment2-card{
    padding: 0 .625em 1.25em;
    &__img{
      margin-bottom: 0;
    }
    &__link{
      font-size: 1em;
      margin-top: .9375em;
    }
    &:not(._show-all){
      .equipment2-card__link:nth-child(3)~.equipment2-card__link{
        display: none;
      }
    }
    &._show-all{
      .equipment2-card__more{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
        svg{
          transform: rotate(-180deg);
        }
      }
    }
    &__more{
      display: flex;
      align-items: center;
      color: $primary;
      margin-top: .9375em;
      span{
        font-size: 1em;
        line-height: 140%;
      }
      ._view2{
        display: none;
      }
      svg{
        width: 1.5em;
        margin-left: .9375em;
      }
    }
  }
}


