.footer{
  padding: 5em 0 3em;
  background-color: $gray;
  color: $white;
  border-radius: .9375em;
  margin-top: .625em;
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 14.6875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__middle{
    display: grid;
    grid-template-columns: 19.375em 19.375em 19.375em calc(100% - 67.5em);
    gap: 3.125em;
    margin-top: 1.875em;
  }
  &__bottom{
    margin-top: 2.5em;
    padding-top: 3em;
    border-top: 1px solid rgba($white,.2);
  }
  &__by{
    width: 4.75em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-nav{
  &__btn{
    display: none;
  }
  &__hidden{
    margin-top: 1.875em;
    @media screen and (min-width: 993px){
      display: block !important;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 120%;
    opacity: .5;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 1.25em);
    font-size: 1em;
    line-height: 120%;
    &:hover{
      text-decoration: underline;
    }
  }
}

.footer-contacts{
  @include grid100gap(1.25em);
  &__item{
    @include grid100gap(.625em);
  }
  &__title{
    opacity: .5;
  }
  &__desc{
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.5em 0 3.5em;
    border-radius: .625em;
    @media screen and (min-width: 577px){
      .double-gis{
        width: 17.8125em;
      }
    }
    &__middle{
      display: block;
      margin-top: .625em;
    }
    &__bottom{
      margin-top: 1.25em;
      padding-top: 1.25em;
      @include grid100gap(1.25em);
    }
  }

  .footer-nav{
    padding: 1.125em .625em;
    border-bottom: 1px solid rgba($white,.1);
    &._active{
      .footer-nav__btn svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__title{
      opacity: 1;
    }
    &__btn{
      width: 1.5em;
      @include center;
      svg{
        width: 100%;
      }
    }
    &__hidden{
      margin-top: 1.75em;
      display: none;
    }
    &__link{
      font-size: .875em;
      @include lastM(0 0 1.43em);
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__top{
      display: block;
    }
    &__logo{
      width: 15.875em;
      margin: 0 0 1em;
    }
  }
}
