@import "../vars";
.tab,
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 3.5em;
    border-radius: .625em;
    text-transform: uppercase;
    font-weight: 700;
    span{
        font-size: 1em;
        letter-spacing: 0.02em;
    }
    &:focus {
        box-shadow: none;
    }
}

.tab,
.m-btn-white{
    background-color: $white;
    color: $gray;
    &:hover{
        background-color: #eb6700;
        color: $white;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: #eb6700;
        color: $white;
    }
}

.m-btn-gray{
    background-color: $gray;
    color: $white;
    &:hover{
        background-color: #283444;
        color: $white;
    }
}

.m-btn-gray-outline{
    color: $gray;
    border: 1px solid #fafafa;
    &:hover{
        border-color: $primary;
        background-color: $primary;
        color: $white;
    }
}
