.error404{
  padding: 3.75em 0 13em;
  &__inner{
    position: relative;
  }
  &__top{
    max-width: 62.5em;
  }
  &__desc{
    margin-top: 1.25em;
    max-width: 30em;
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
  }
  &__link{
    margin-right: 1.25em;
    &:last-child{
      margin: 0;
    }
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: -11em;
    width: 44.125em;
  }
}


@media screen and (max-width: 992px){
  .error404{
    padding: 2.5em 0 .625em;
    .container{
      padding: 0;
    }
    &__nav{
      margin-top: 1.25em;
      @include grid100gap(.625em);
    }
    &__link{
      margin: 0;
      width: 100%;
    }
    &__img{
      position: static;
      width: calc(100% + 1.25em);
      margin-left: -.625em;
    }
  }
}


