.m-badge{
  padding: 0 1em;
  height: 2.125em;
  border-radius: .625em;
  @include center;
  background-color: $black;
  color: $white;
  display: flex;
  align-items: center;
  img{
    margin-right: .625em;
    width: 1.5em;
  }
  span{
    font-size: .9375em;
  }
  &._big{
    height: 2.5em;
    span{
      font-size: 1.125em;
    }
  }
  &._orange{
    background-color: $primary;
  }
  &._green{
    background-color: $green;
  }
}

.catalog-card{
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  &__img{
    @include imgCover(63.4%);
    border-radius: .9375em;
    margin-bottom: 1.25em;
  }
  &__badges{
    position: absolute;
    top: .625em;
    left: .625em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .m-badge{
      margin-bottom: .625em;
    }
  }
  &__badges2{
    position: absolute;
    top: .625em;
    right: .625em;
  }
  &__type{
    font-weight: 600;
    opacity: .5;
  }
  &__info{
    display: flex;
    align-items: center;
  }
  &__title{
    text-transform: uppercase;
  }
  &__year{
    padding: 0 .625em;
    margin-left: 1.25em;
  }
  &__desc{
    margin-top: .625em;
    opacity: .5;
  }
  &__price{
    margin-top: .625em;
    &-title{
      opacity: .5;
    }
    &-num{
      margin-left: .5em;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.25em;
  }
  &__link{
    width: 100%;
  }
}

.req{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 30em) 29.375em;
    gap: 2.5em .625em;
  }
  &__info{
    @include grid100gap(.625em);
  }
  &__info2{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    display: flex;
    align-items: center;
    white-space: nowrap;
    svg{
      width: 3.125em;
      margin-right: .625em;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
  &__btn{
    margin-top: 1.25em;
    width: 100%;
  }
}

.collection-slider{
  margin-top: 1.875em;
  .swiper{
    &-slide{
      width: 50%;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-card{
    &__img{
      border-radius: .625em;
      margin-bottom: .625em;
    }
    &__badges{
      .m-badge{
        margin-bottom: .3125em;
      }
    }
    &__badges2{
      .m-badge{
        height: 2.0625em;
        span{
          font-size: .875em;
        }
        img{
          width: 1em;
        }
      }
    }
    &__info{
      justify-content: space-between;
    }
    &__price{
      &-title{
        display: none;
      }
      &-num{
        margin: 0;
      }
    }
  }

  .req{
    &__grid{
      grid-template-columns: 100%;
    }
    &__info{
      gap: 1.25em;
    }
    &__link{
      .h3{
        font-size: 1.625em;
      }
      svg{
        width: 2.5em;
      }
    }
    &__btn{
      margin-top: .625em;
    }
  }

  .collection-slider{
    margin-top: 1.25em;
    .swiper{
      &-slide{
        width: 19.75em;
      }
    }
  }
}


