.sales{
  @media screen and (min-width: 577px){
    .h2 br{
      display: none;
    }
  }
  &__link{
    margin-left: 3em;
  }
}

.story-slider,
.slider-line,
.slider{
  .swiper{
    &-container{
      width: calc(100% + .625em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      padding-right: .625em;
    }
  }
}

.sales-slider{
  margin-top: 1.875em;
  .swiper{
    &-slide{
      width: 25%;
    }
  }
}

.sale-card{
  padding: .625em .625em 1.25em;
  border-radius: 1.125em;
  background: $white;
  height: 100%;
  &__img{
    @include imgCover(60.6%);
    border-radius: .9375em;
  }
  &__content{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 1em;
  }
  &__link{
    margin-left: 3em;
  }
  &__date{
    margin-bottom: .625em;
  }
}

.m-btn-arrow{
  padding: 0;
  width: 2.75em;
  height: 2.75em;
  flex-shrink: 0;
  img{
    width: 1.25em;
  }
}

.spare-card{
  background-color: $white;
  padding: 2.5em 1.875em;
  border-radius: .9375em;
  display: flex;
  flex-direction: column;
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.25em;
    width: calc(100% + .625em);
    position: relative;
    z-index: 2;
  }
  &__tag{
    margin: .625em .625em 0 0;
    padding: 0 2em;
    height: 3.1875em;
    border: 1px solid $gray;
    background-color: $white;
    border-radius: 31.25em;
    @include center;
    &:hover{
      background-color: $gray;
      color: $white;
    }
  }
  &__car{
    width: 19.5em;
    display: block;
    margin: -5em 0 0 auto;
  }
  &__nav{
    margin-top: auto;
  }
  &__brands{
    margin-top: 1.875em;
    width: 100%;
    display: block;
  }
}

.brands-slider{
  @media screen and (min-width: 993px){
    .swiper{
      &-container{
        width: 100%;
      }
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(4,calc(25% - 2.625em / 3));
        gap: .875em;
      }
      &-slide{
        width: 100%;
        padding-right: 0;
      }
    }
  }
}

.brand-card{
  height: 9.375em;
  background-color: $grayBg;
  border-radius: 1.25em;
  @include center;
  padding: 1.25em;
  img{
    mix-blend-mode: multiply;
    max-width: 100%;
    max-height: 100%;
  }
}


@media screen and (max-width: 992px){
  ._arrow-mob{
    background-image: url(../img/arrow-right.svg);
    @include bgImgSize(1.25em);
    width: 2.5em;
    height: 2.5em;
    padding: 0;
    flex-shrink: 0;
    span{
      display: none;
    }
  }

  .sales-slider{
    margin-top: 1.25em;
    .swiper{
      &-slide{
        width: 18.75em;
      }
    }
  }

  .sale-card{
    border-radius: .625em;
    &__img{
      border-radius: .625em;
    }
    &__content{
      margin-top: .625em;
    }
    &__link{
      margin-left: .5em;
    }
  }

  .spare{
    &__grid{
      margin-top: 1.25em;
    }
  }

  .spare-card{
    border-radius: .625em;
    padding: 1.875em .625em 1.25em;
    &__nav{
      padding: 0 .625em;
    }
    &__tags{
      margin-top: .9375em;
      width: calc(100% + .3125em);
    }
    &__tag{
      margin: .3125em .3125em 0 0;
      padding: 0 .9375em;
      height: 2.375em;
      span{
        font-size: .75em;
      }
    }
    &__car{
      width: 13.625em;
      margin: 0 auto -.5em;
    }
    &__nav{
      position: relative;
      z-index: 2;
    }
    &__brands{
      margin: 1.25em 0;
    }
  }

  .brands{
    @include fullScreen;
  }

  .brands-slider{
    .swiper{
      &-slide{
        width: 15em;
      }
    }
  }

  .brand-card{
    height: 5em;
    border-radius: .625em;
  }
}


@media screen and (max-width: 576px){
  .brands-slider{
    .swiper{
      &-slide{
        width: 10.625em;
      }
    }
  }
}
