.category{
  &__text{
    flex-grow: 1;
  }
  &__link{
    margin-left: 2.5em;
  }
}

.category-slider,
.stories-slider{
  .swiper{
    &-slide{
      width: 25%;
    }
  }
}

.category-slider{
  margin-top: 1.875em;
  .category-card{
    background-color: $white;
    @media screen and (min-width: 993px){
      &__title{
        .text30{
          font-size: 1.375em;
        }
      }
      &__img{
        height: 12em;
      }
    }
  }
}

.story-card{
  cursor: pointer;
  &._open{
    &~.story-modal{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._new{
    .story-card__top{
      background-image: url(../img/radial.svg);
      background-color: transparent;
      background-size: cover;
    }
  }
  &__top{
    border-radius: .9375em;
    background-color: #D7D7D7;
    padding: .3125em;
    margin-bottom: .625em;
  }
  &__img{
    @include imgCover(40%);
    border-radius: .75em;
  }
  &__title{
    text-align: center;
  }
}

._z2{
  position: relative;
  z-index: 2;
}

.main{
  position: relative;
  background-image: url(../img/main-bg.svg);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 81.25em auto;
  &__inner{
    padding: 4.375em 0 1.875em;
    min-height: 46.5625em;
    display: flex;
    flex-direction: column;
  }
  &__top{
    max-width: 65.125em;
  }
  &__desc{
    max-width: 22.1875em;
  }
  &__nav{
    margin-top: 1.875em;
    width: 18.75em;
    max-width: 100%;
    @include grid100gap(.625em);
  }
  .double-gis{
    margin-top: auto;
  }
  &__img{
    position: absolute;
    top: 12.5em;
    right: -8em;
    width: 95.875em;
    max-width: 70%;
    img{
      width: 100%;
      display: block;
    }
  }
}

.double-gis{
  width: 17.8125em;
  img{
    width: 100%;
    display: block;
  }
}


@media screen and (max-width: 992px){
  .category{
    &__top{
      align-items: flex-start;
    }
    &__text{
      @include grid100gap(.5625em);
    }
    &__link{
      margin-left: 1em;
      margin-top: -.3125em;
      background-color: $primary;
    }
  }

  .category-slider {
    margin-top: 1.25em;
    .swiper {
      &-slide {
        width: 18.75em;
      }
    }
  }

  .stories{
    @include fullScreen;
    border-radius: 0;
  }

  .stories-slider {
    .swiper {
      &-slide {
        width: 17.0625em;
      }
    }
  }

  .story-card{
    &__top{
      padding: .1875em;
      border-radius: .625em;
    }
    &__img{
      border-radius: .5625em;
    }
  }

  .main{
    background-size: 110% auto;
    background-position: right 1em bottom 10em;
    &__inner{
      padding: 2.5em 0;
      min-height: unset;
    }
    &__img{
      position: static;
      width: 100%;
      max-width: unset;
      margin-top: 2em;
    }
    &__top,
    &__desc{
      max-width: 100%;
    }
    &__nav{
      width: 100%;
    }
  }

  .double-gis{
    width: 100%;
  }
}


@media screen and (max-width: 576px){
  .main{
    &__img{
      width: 120%;
      margin-right: -20%;
    }
  }
}
