@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._mt40{
  margin-top: 2.5em;
}

._mt30{
  margin-top: 1.875em;
}

._mt20{
  margin-top: 1.25em;
}

._flex,
._flex-between{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(-90deg, rgba(#050a10,0) 0%, #050a10 100%);
    pointer-events: none;
    border-radius: inherit;
  }
}

