.breadcrumbs {
    margin-bottom: 2em;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        position: relative;
        display: inline;
        color: rgba($white,.5);
        &:not(:first-child) {
            margin-left: 2.25em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                top: .625em;
                right: 100%;
                margin-right: .6875em;
                width: 1em;
                height: 1px;
                border-radius: 1px;
                background-color: rgba($white,.5);
            }
        }
        &:last-child{
            a{
                pointer-events: none;
            }
        }
    }
    a{
        font-size: .875em;
        line-height: 121%;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        margin-bottom: .625em;
        @include fullScroll;
        padding-bottom: .625em;
        white-space: nowrap;
    }
}


