.equipment-table{
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th{
    text-align: left;
    padding-bottom: .625em;
    border-bottom: 1px solid $gray;
  }
  @media screen and (min-width: 577px){
    th,td{
      &:nth-child(2){
        width: 30em;
      }
      &:nth-child(3){
        width: 13.75em;
      }
    }
    td{
      padding: .625em 0 1.25em;
    }
  }
}

.equipment-row{
  border-bottom: 1px solid rgba($gray,.1);
  &__content{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 6.25em;
    height: 6.25em;
    border-radius: .9375em;
    @include center;
    padding: 1em;
    background-color: $grayBg;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__info{
    @include grid100gap(.625em);
  }
  .m-badge{
    display: inline-flex;
    font-weight: 600;
  }
}

.price-list,
.call-confirm{
  padding: 1.875em;
  border-radius: .9375em;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.price-list{
  color: $white;
  background-color: $gray;
  &__nav{
    margin-top: auto;
    padding-top: 1.25em;
  }
  &__link{
    height: 3.75em;
    .price-list__link-size{
      font-size: .875em;
      font-weight: 400;
    }
    img{
      margin: 0 .625em;
      width: 1.5em;
    }
  }
}

.call-confirm{
  background-color: $grayBg;
  .req__link{
    color: $primary;
    margin: 2em 0 0;
    path{
      stroke: $primary;
    }
    &:hover{
      color: $gray;
      path{
        stroke: $gray;
      }
    }
  }
  &__desc{
    margin-top: .625em;
  }
}

.product-desc2{
  @media screen and (min-width: 993px){
    .about{
      &__grid {
        grid-template-columns: 51.75em calc(100% - 52em);
      }
      &__content{
        max-width: unset;
      }
      &__img {
        width: calc(100vw - (100vw - 1450px) / 2 - 3.125em - 52em);
      }
    }
    .text{
      max-width: 44.375em;
    }
  }
}

.slider-line{
  .swiper-wrapper {
    -webkit-transition-timing-function: linear !important;
    transition-timing-function: linear !important;
  }
}

.equipment-line{
  border-radius: 0;
  @include fullScreen;
}

.equipment-slider{
  .swiper-slide{
    width: calc(100% / 3);
  }
}

.equipment-card{
  padding: .625em .625em 1.25em;
  border-radius: 1.125em;
  background-color: $grayBg;
  position: relative;
  &__img{
    border-radius: .9375em;
    background-color: $white;
    padding: 1em 1.875em;
    @include center;
    height: 16em;
    margin-bottom: .9375em;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__content{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    margin-left: 1em;
  }
  .m-badge{
    top: 1.25em;
    right: 1.25em;
    position: absolute;
    font-weight: 600;
  }
  &__info{
    @include grid100gap(.625em);
  }
  &__price{
    white-space: nowrap;
    margin-left: 1em;
  }
}


@media screen and (max-width: 992px){
  .equipment-table{
    @media screen and (min-width: 577px){
      th,td{
        &:nth-child(2),
        &:nth-child(3){
          width: 12em;
        }
      }
    }
  }

  .price-list{
    padding: 2.5em .625em;
    &__nav{
      width: 100%;
    }
    &__link{
      height: 3.25em;
      padding: 0;
      width: 100%;
    }
  }

  .call-confirm{
    padding: 2.5em .625em;
    &__desc{
      margin-top: 1.25em;
      padding: 0 1em;
    }
    .req__link{
      margin-top: 1.875em;
    }
  }

  .equipment-slider{
    .swiper-slide{
      width: 18.75em;
    }
  }

  .equipment-card{
    border-radius: .625em;
    &__img{
      border-radius: .625em;
      height: 10.25em;
      margin-bottom: .625em;
    }
    .m-badge{
      height: 1.5625em;
      padding: 0 .625em;
      border-radius: .3125em;
      span{
        font-size: .875em;
      }
      img{
        width: 1em;
        margin-right: .25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .equipment-table{
    table,
    tbody,
    td{
      display: block;
    }
    thead{
      display: none;
    }
  }

  .equipment-row{
    @include grid100gap(.625em);
    padding: .625em 0 .625em 8.4375em;
    min-height: 9.0625em;
    position: relative;
    &__img{
      position: absolute;
      width: 7.8125em;
      height: 7.8125em;
      left: 0;
      top: .625em;
    }
    &__info{
      gap: .25em;
    }
    td{
      &:nth-child(3){
        order: -1;
      }
    }
    .m-badge{
      height: 1.5625em;
      padding: 0 .625em;
      border-radius: .3125em;
      span{
        font-size: .875em;
      }
      img{
        width: 1em;
        margin-right: .25em;
      }
    }
  }
}
