.page-block{
  padding: 5em 0;
}

.sales-page{
  .sale-card{
    background-color: $grayBg;
  }
}

.page-top{
  padding: 1.25em 0 4.375em;
  background-image: url(../img/page-top-bg.svg);
  background-repeat: no-repeat;
  background-position: left bottom;
  background-size: 55.5em auto;
  &__info{
    position: relative;
    display: inline-block;
  }
  &__count{
    padding: 0 1em;
    position: absolute;
    right: 0;
    bottom: 100%;
    height: 2.0625em;
    border-radius: .5em;
    background-color: $white;
    @include center;
    color: $gray;
  }
}

.text-page{
  padding-bottom: 0;
}

.text-block{
  &__desc{
    margin-top: 1.1em;
    font-size: 1.375em;
    line-height: 160%;
  }
  .text{
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .page-block{
    padding: .625em 0 2.5em;
  }

  .page-top{
    padding: 1.25em 0;
    &__count{
      padding: 0 .5em;
      height: 1.5em;
    }
  }

  .text-block{
    &__desc{
      margin-top: 1.25em;
      font-size: 1em;
    }
    .text{
      li{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .page-top{
    background-size: 130% auto;
    background-position: center bottom;
    &__count{
      bottom: 0;
    }
  }
}
